.ButtonCounter {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 25px;
}
.buttonInc {
    font-size: 20px;
    font-weight: bold;
    width: 200px;
    height: 50px;
    border: 1px solid black;
    border-radius: 5px;
    cursor: pointer
}
.buttonReset {
    font-size: 20px;
    font-weight: bold;
    width: 200px;
    height: 50px;
    border: 1px solid black;
    border-radius: 5px;
    cursor: pointer
}
